import React from 'react'
import '../css/HomeDetails.css'
import Nav from './Nav';
import Siteplan from './Siteplan';
import Features from './Features';
import SimilarAvailableHomes from './SimilarAvailableHomes';
import $ from "jquery";


export class HomeDetails extends React.Component {
  constructor(props) {
    super();
    this.state = {
      marketingName: props.match.params.marketingName,
      elevationSelected: 'A',
      homeChanged: false,
      optShowing: false,
      optCopy: "",
      optOriginalElevation: 'A'
    }
  }

  componentWillUpdate(nextProps, nextState){
    if(this.state.marketingName != nextProps.match.params.marketingName) {
      this.setState({
        marketingName: this.props.match.params.marketingName
      });
      $('html, body').animate({ scrollTop: 0 }, '2000');
    }
  }

  componentWillMount(){
    $('html, body').animate({ scrollTop: 0 }, '0');
    //hard coding for Meadow, show opt first
    if(this.state.marketingName == "Meadow" && this.state.elevationSelected == 'A') {
      this.setState({
        elevationSelected: 'A OPT',
        optCopy: 'OPT. 1 Bedroom Plan'
      });
    }


  }
  updateActiveElevation(elevation) {
    var optOriginalElevation = elevation;
    //hard coding for showing Meadow OPT first
    if(this.state.marketingName == "Meadow" && elevation == "A OPT"){
      elevation = 'A';
    }
    else if(this.state.marketingName == "Meadow" && elevation == "A") {
      elevation = 'A OPT';
      optOriginalElevation = 'A';

    }
    else if(this.state.marketingName == "Meadow" && elevation == "B") {
      elevation = 'B OPT';
      optOriginalElevation = 'B';
    }
    else if(this.state.marketingName == "Meadow" && elevation == "B OPT") {
      elevation = 'B';
    }
    this.setState({
      elevationSelected: elevation,
      optOriginalElevation: optOriginalElevation,
      optShowing: false
    });

  }
  toggleOptElevation(elevation, optPlans) {
    var optCopy;
    if(optPlans == "n/a"){
      optCopy = this.state.optCopy;
    }
    else {
      optCopy = optPlans;
    }
    //hard coding for showing Meadow OPT first
    if(this.state.marketingName == "Meadow" && elevation == "A OPT"){
      elevation = 'A'
    }
    else if(this.state.marketingName == "Meadow" && elevation == "A") {
      elevation = 'A OPT'
    }
    else if(this.state.marketingName == "Meadow" && elevation == "B") {
      elevation = 'B OPT'
    }
    else if(this.state.marketingName == "Meadow" && elevation == "B OPT") {
      elevation = 'B'
    }
    this.setState({
      elevationSelected: elevation,
      optShowing: !this.state.optShowing,
      optCopy: optCopy
    });

  }


  renderElevationToggleButtons() {
    var data = require('../homes.json');

    return (
      data.map(function(item, index){
        if(this.state.marketingName == item.marketingName && (item.elevation.indexOf('OPT') == -1)) {
          var activeClass;
          //change style of active and inactive buttons
          if(item.elevation == this.state.optOriginalElevation) {
              activeClass = 'elevationToggleButtonActive';
          }
          else {
            activeClass = 'elevationToggleButtonInactive';
          }
          return <div className={"elevationToggleButton "+item.category+"-backgroundColor " + activeClass} onClick={() => this.updateActiveElevation(item.elevation)}> Elevation {item.elevation}</div>
        }
      },this)
    );
  }

  renderOptButton(marketingName, optPlans) {
    var viewOrHide;

    var optCopy;
    if(optPlans == "n/a"){
      optCopy = this.state.optCopy;
    }
    else {
      optCopy = optPlans;
    }


    var optClass = this.state.optOriginalElevation + ' OPT';

    var data = require('../homes.json');
    return (
      data.map(function(item, index){
        if(marketingName == item.marketingName && (item.elevation == optClass)) {
          var optLink;

          if(this.state.optShowing){
            viewOrHide = "Hide";
            optLink = this.state.optOriginalElevation;
          }else {
            viewOrHide = "View";
            optLink = item.elevation;
          }


          return <div className="optButton" onClick={() => this.toggleOptElevation(optLink, optPlans)}>{viewOrHide} {optCopy}</div>
        }
      },this)
    );
  }

  navigateFloorplans(direction) {
    if(direction == 'left') {
      $(".floorplansContainerInner").animate({"right":"0%"}, "slow");
      $(".floorplanIndicatorRight").removeClass("floorplanIndicatorActive");
      $(".floorplanIndicatorLeft").addClass("floorplanIndicatorActive");
      $(".floorplanNavigatorLeft").hide();
      $(".floorplanNavigatorRight").show();
    } else {
      $(".floorplansContainerInner").animate({"right":"89%"}, "slow");
      $(".floorplanIndicatorLeft").removeClass("floorplanIndicatorActive");
      $(".floorplanIndicatorRight").addClass("floorplanIndicatorActive");
      $(".floorplanNavigatorLeft").show();
      $(".floorplanNavigatorRight").hide();
    }
  }

  renderCalloutContainer(){
    return(
      <div className="calloutContainer">
          <div className="calloutContainerInner">
            <div className="calloutImage"><img/></div>
            <p className="calloutText"></p>
          </div>
          <div className="calloutBottomSliver"><img src={"../images/callouts/calloutBottomSliver.png"}/></div>
      </div>
    );
  }
  calloutClicked(e,calloutID, x, y,floorplanType){
    var data = require('../callouts.json');
    var top = $(e.target).parent().css("top");
    var left = $(e.target).parent().css("left");
    return (
      data.map(function(item, index){
        if(item.id == calloutID) {
          $(".calloutContainer").hide();

          var imageURL = "../images/callouts/"+item.img;
          $(".calloutImage img").attr("src", imageURL);
          $(".calloutText").html(item.text);
          $(".calloutContainer").css("top", top);
          $(".calloutContainer").css("left", left);
          $("."+floorplanType+" .calloutContainer").show();
          $("."+floorplanType+" .calloutContainer").css("opacity","0");
          $("."+floorplanType+" .calloutContainer").animate({ opacity: 1}, '2000');
        }
      },this)
    );
  }
  renderCallouts(floorplan, floorplanType) {
    var data = require('../callouts.json');

    return (
      data.map(function(item, index){

        if(item.floorplan == floorplan) {
          const style = {
            top: item.y+"%",
            left: item.x+"%"
          }
          return <div className="calloutIcon" style={style} onClick={(e) => this.calloutClicked(e,item.id, item.x, item.y, floorplanType)} data-id="calloutIcon">
              <img src={"../images/callouts/calloutIcon.png"} data-id="calloutIcon"/>
            </div>
        }
      },this)
    );
  }

  closeCalloutContainer(e){
    if($(e.target).attr("data-id") != "calloutIcon"){
      $(".calloutContainer").hide();
    }
  }

  renderElevation() {
    var data = require('../homes.json');


    return (
    data.map(function(item, index){
      if((this.state.marketingName == item.marketingName) && (this.state.elevationSelected == item.elevation)) {
        var floorplans = item.floorplans.split(",");


        return <div className="renderedElevation" onClick={(e) => this.closeCalloutContainer(e)}>
                 <div className="elevationImageContainer">
                    <img src={"../images/homes/"+item.renderings}/>
                 </div>

                 <div className={"infoContainer "+item.category+"-backgroundColor"}>
                    <div className="elevationToggleButtonContainer">
                       {this.renderElevationToggleButtons()}
                    </div>
                  <div>
                    <nav>
                      <ul>
                        <li onClick={() => this.props.history.push('/')} className="productDetailsIcon backbutton"><img src={"../images/homes/productBackButton.png"}/></li>
                        <li className="productDetailsIcon productName">{item.category}: </li>
                        <li className="productStyle">{item.marketingName} style {item.elevation}</li>
                        <li className="productDetailsIcon productPrice">${item.price}</li>
                        <li className="productDetailsIcon productSquareFoot">{item.sqFtMarketing} sq. ft.</li>
                        <li className="productDetailsIcon productBedroom"><img className="productIcon" src={"../images/homes/bedroomIcon.png"}/>{" "}{item.beds}</li>
                        <li className="productDetailsIcon productBathroom"><img className="productIcon" src={"../images/homes/productBathroomIcon.png"}/>{" "}{item.baths}</li>
                        <li className="productDetailsIcon productSignupBtn"><img className="signupBtn" onClick={() => this.props.history.push('/')} src={"../images/homes/productSignupBtn.png"}/></li>

                      </ul>
                    </nav>
                  </div>
                 </div>
                 <div>
                    <img className="mobileSignupBtn" src={"../images/homes/productSignupBtn.png"}/>
                 </div>
                 <div className="floorplansContainer">
                    <div className="floorplansContainerInner">
                      <div className="floorplan ground"><img src={"../images/floorplans/"+floorplans[1]+""}/>{this.renderCallouts(floorplans[1], "ground")}{this.renderCalloutContainer()}</div>
                      <div className="floorplan basement"><img src={"../images/floorplans/"+floorplans[0]+""}/>{this.renderCallouts(floorplans[0], "basement")}{this.renderCalloutContainer()}</div>

                   </div>
                   <div className="floorplanNavigator floorplanNavigatorLeft" onClick={() => this.navigateFloorplans("left")}><img src={"../images/left-arrow.png"}/></div>
                   <div className="floorplanNavigator floorplanNavigatorRight" onClick={() => this.navigateFloorplans("right")}><img src={"../images/right-arrow.png"}/></div>

                    {this.renderOptButton(item.marketingName, item.optPlans)}
                    <div className="spacerForFloats" ></div>
                </div>
                <div className="floorplanIndicator floorplanIndicatorLeft floorplanIndicatorActive" onClick={() => this.navigateFloorplans("left")}><div className="floorplanIndicatorInner"></div></div>
                <div className="floorplanIndicator floorplanIndicatorRight" onClick={() => this.navigateFloorplans("right")}><div className="floorplanIndicatorInner"></div></div>
              </div>
      }

      },this)
    );
  }

  render() {
    const infoContainerColor = '';
    return (
      <div>
        <Nav/>
        {this.renderElevation()}
          <Siteplan/>
          <SimilarAvailableHomes history={this.props.history} currentProduct={this.state.marketingName}/>
          <Features/>
      </div>
    );
  }
};

export default HomeDetails;



