import React from 'react'
import Nav from './Nav';
import Header from './Header';
import Contact from './Contact';
import SiteMap from './SiteMap';
import Sidebar from './Sidebar';
import Upgrades from './Upgrades';
import Carefree from './Carefree';
import Trails from './Trails';
import AreaMap from './AreaMap';
import Siteplan from './Siteplan';
import Features from './Features';
import HomeDetails from './HomeDetails';
import Homes from './Homes';
import '../css/Stickey.css'
import $ from 'jquery'

export class MainHome extends React.Component {
  render() {
    return (
      <div>
        <Nav/>
        <Header history={this.props.history}/>
        <Contact/>
        <SiteMap/>
        <Upgrades/>
        <Carefree/>
        <Trails/>
        <AreaMap/>
        <Homes history={this.props.history}/>
        <Siteplan/>
        <Features/>
        <Upgrades/>
      </div>

    );
  }
componentWillMount () {
  $('html, body').animate({ scrollTop: 0 }, '0');
  $(document).ready (function() {
  var distance = $('#contact-container').offset().top;
  var navHeight = $('#contact-container').height();
        $(window).scroll(function() {
            if ( ($(this).scrollTop()) >= distance ) {
                $('.side_box, .contact-container').addClass('sideBarStuck');
                $('.logo-container').addClass('logoStick');
                $('.side_box').css('top', navHeight);
                $('.help-content').hide();
            }
            else if ( ($(this).scrollTop()) <= distance ) {
                $('.side_box, .contact-container').removeClass('sideBarStuck');
                $('.side_box').css('top', 'auto');
                $('.logo-container').removeClass('logoStick');
                $('.help-content').show();

              }
        });
    });
  }
}

export default MainHome;



