import React from 'react'
import '../css/SimilarAvailableHomes.css'
import Home from './Home'

export class SimilarAvailableHomes extends React.Component {

  constructor(){
    super();
    this.state = {
      closestPricedHomes: {},
      similarHomesState: null
    }
  }


  getSimilarHomes(){
    var data = require('../homes.json');
    // var currentPrice = data[data.indexOf(this.props.currentProduct)].price;
    var currentProductLocation;
    var counter = 0;
    data.map(function(item, index){
        counter++;
        if(item.marketingName == this.props.currentProduct){
           currentProductLocation = counter;
        }
    },this)

    return currentProductLocation;
  }


  renderHomes() {
    var data = require('../homes.json');
    var counter = 0;
    // var currentProductLocation = this.getSimilarHomes();
    //
    // if(currentProductLocation > 2){
    //   var firstSimProd = currentProductLocation - 1;
    //   var secondSimProd = currentProductLocation - 2;
    //   var thirdSimProd = currentProductLocation + 1;
    // }
    // else {
    //   var firstSimProd = currentProductLocation + 1;
    //   var secondSimProd = currentProductLocation + 2;
    //   var thirdSimProd = currentProductLocation + 3;
    // }



    return (
      data.map(function(item, index){

          if((counter < 3) && (item.elevation = 'A') && (item.marketingName != this.props.currentProduct)){
            counter++;
            return <div className="col-xs-12 col-sm-4 col-md-4 "><Home
              Name={item.marketingName}
              SquareFootage={item.sqFtMarketing}
              MinPrice={item.price}
              NumBath={item.baths}
              NumBed={item.beds}
              history={this.props.history}
              homeID={item.id}
              image={item.renderings}
            /></div>
            }
      },this)

    );
  }


  render() {
    return (
      <div className='similarAvailableHomes'>
          <div className="similarAvailableHomesHeading">
            <img src="/images/similar-available-homes-parallelogram.png"/>
              Similar Available Homes
          </div>

          <div className="homesContainer">
            <div className="homesContainerInner col-xs-12">
              {this.renderHomes()}
            </div>
          </div>
      </div>

    );
  }


}

export default SimilarAvailableHomes;



