import React from 'react'
import '../css/Homes.css'
import { BrowserRouter as Router } from 'react-router-dom'


export default class Home extends React.Component {
  render() {
    return(
      <div className="homeItemContainer">
        <div className="thumbnail" onClick = {this.props.onClick}>
            <img className="group list-group-image" src={"/images/homes_small/"+this.props.image} alt="" />

            <div className="caption captionRow">
                <div className="row">
                    <div className="col-xs-6 col-md-6">
                       <h4 className="group inner list-group-item-heading productTitle">
                    {this.props.Name}</h4>
                    </div>
                   <div className="col-xs-6 col-md-6 productTitlePrice">
                        <span className="productSubTitle">from</span><span className="productSubPrice">${this.props.MinPrice}</span>
                    </div>
                </div>
                <div className="row">
                    <div className="col-xs-4 col-md-4  productTitleFeet">
                        <span className="productSubPrice">{this.props.SquareFootage}</span><span className="productSubTitle">sq.ft.</span>
                    </div>
                <div className="col-xs-4 col-md-4 productTitlePrice">
                        <span className="productSubPrice">{this.props.NumBed}<img className="img-responsive bedIcon"src="/images/bedroomIcon.png"/></span>
                    </div>
                    <div className="col-xs-4 col-md-4 productTitlePrice">
                        <span className="productSubPrice">{this.props.NumBath}<img className="img-responsive bathIcon"src="/images/bathIcon.png"/></span>
                    </div>
                </div>
            </div>
        </div>
      </div>
    );
  }

};



