"use strict"
import React from 'react'
import Nav from './Nav'
import '../css/reservation.css'
import { BrowserRouter as Router } from 'react-router-dom'
import $ from 'jquery';
import Home from './Home'
import HomeDetails from './HomeDetails'



export default class Reservation extends React.Component {
  constructor(){
    super();
    this.state = {
      activeStep: '2',
      step1Data: {complete:false},
      step2Data: {complete:false, homesData:{}},
      step3Data: {complete:false},
      activeStepData: {},
    }
  }
  componentWillMount(){
    this.getStepData();
  }
  getStep2Data(handleReturn){
    var self = this;
    this.getHomes(function(homeData){
      handleReturn(
          homeData.map(function(item, index){
            if(item.elevation == 'A'){
                return <div className="col-xs-12 col-sm-4 col-md-4 ">
                <Home
                  Name={item.marketingName}
                  SquareFootage={item.sqFtMarketing}
                  MinPrice={item.price}
                  NumBath={item.baths}
                  NumBed={item.beds}
                  history={self.props.history}
                  homeID={item.id}
                  image={item.renderings}
                /></div>
            }
          },this)
      );
    },"town");
  }
  renderStep2(){
    return this.state.step2Data.homesData;
  }

  renderStep1(){
    $(".homeLoadingGif").hide();
    return(<div>
        <div className="homesCategoryContainer"><button onClick={() => this.completeStep(1)}>TOWNS</button></div>
        <div className="homesCategoryContainer"><button onClick={() => this.completeStep(1)}>SEMIS</button></div>
      </div>
    );
  }

  completeStep(step){
    switch(step){
      case 1:
        this.setState({
          activeStep: '2'
        },function(){
            this.getStepData();
        }.bind(this));
        break;
    }

  }

  getStepData(){
    var self = this;
    switch(this.state.activeStep){
      case '2':
        this.getStep2Data(function(returnData){
          //add homes to state
          var step2Data = {...self.state.step2Data}
          step2Data.homesData = returnData;
          self.setState({
            step2Data: step2Data
          });
          alert(JSON.stringify(self.state.step2Data.homesData));
          $(".homeLoadingGif").hide();
        });
        
        break;
      case 1:
        this.renderStep1();
        break;
    }
  }


  getHomes(handleHomeData,homeCategory){
    $.ajax({
        type: 'POST',
        url: '/getHomes',
        data: { 
            'category': 'town' 
        },
        success: function(data,status){
          handleHomeData(data);
        }
    });
  }
  renderStep(){
    var self = this;
    switch(this.state.activeStep){
      case 2:
        return this.renderStep2();
        break;
      case 1:
        return this.renderStep1();
        break;
    }
  }
  renderStepIndicator(step) {
    var activeClass = '';

    if(this.state["step"+step+"Data"].complete) {

    }
    else if(step == this.state.activeStep) {
      activeClass = 'stepIndicatorActive';
    }
    return(
      <div className={"stepIndicator " + activeClass}>Step {step}</div>
    );

  }
  render() {
    return(
      <div>
        <Nav/>
        <div className="reservationContainer">
          <div className="stepIndicatorBar">
            {this.renderStepIndicator(1)}
            {this.renderStepIndicator(2)}
            {this.renderStepIndicator(3)}
          </div>
          <div className="stepContainer">
            {this.renderStep()}
            <div className="homeLoadingGif"><img src={"/images/homeLoading.gif"}/></div>
          </div>
        </div>
      </div>
    );
  }
};



